import React, { useState } from 'react';
import Chart from 'react-apexcharts';
const salesData = {
    options: {
      chart: {
        id: 'sales-chart',
      },
      xaxis: {
        categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
      },
    },
    series: [
      {
        name: 'Sales',
        data: [100, 200, 150, 300, 200, 250, 400, 350, 300, 450, 400, 500],
      },
    ],
  };
  
  const ordersData = {
    options: {
      chart: {
        id: 'orders-chart',
      },
      xaxis: {
        categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
      },
    },
    series: [
      {
        name: 'Orders',
        data: [10, 20, 15, 30, 20, 25, 40, 35, 30, 45, 40, 50],
      },
    ],
  };
  
  const ECommerce = () => {
    const [salesChart, setSalesChart] = useState(salesData);
    const [ordersChart, setOrdersChart] = useState(ordersData);
  
    return (
      <div>
        <h1>Ecommerce Dashboard</h1>
        <div>
          <Chart options={salesChart.options} series={salesChart.series} type="line" height={350} />
        </div>
        <div>
          <Chart options={ordersChart.options} series={ordersChart.series} type="bar" height={350} />
        </div>
      </div>
    );
  };
  
  export default ECommerce;
  